import React, { useCallback } from "react";
import { makeStyles } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Container from "@material-ui/core/Container";
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import Typography from "@material-ui/core/Typography";
import Form from "@rjsf/material-ui";
import * as fileDownload from "js-file-download";
import { useDropzone } from "react-dropzone";

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {"Copyright © "}
            《少年电脑世界》游戏梦工厂 {new Date().getFullYear()}
            {"."}
        </Typography>
    );
}

const useStyles = makeStyles((theme) => ({
    appBar: {
        position: "relative",
    },
    layout: {
        width: "auto",
        marginLeft: theme.spacing(2),
        marginRight: theme.spacing(2),
        [theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
            width: 600,
            marginLeft: "auto",
            marginRight: "auto",
        },
    },
    paper: {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(3),
        padding: theme.spacing(2),
        [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
            marginTop: theme.spacing(6),
            marginBottom: theme.spacing(6),
            padding: theme.spacing(3),
        },
    },
    stepper: {
        padding: theme.spacing(3, 0, 5),
    },
    buttons: {
        display: "flex",
        justifyContent: "flex-end",
    },
    button: {
        marginTop: theme.spacing(3),
        marginLeft: theme.spacing(1),
    },
}));

export default function Main() {
    const classes = useStyles();
    const schema = {
        title: "编辑任务",
        type: "array",
        items: {
            type: "object",
            required: ["ID", "Name", "IconID", "Description", "Steps"],
            properties: {
                ID: { type: "number", title: "编号" },
                Name: { type: "string", title: "任务名称" },
                IconID: { type: "number", title: "任务图标" },
                Description: { type: "string", title: "任务描述" },
                Steps: {
                    type: "array",
                    title: "任务步骤",
                    items: { type: "string" },
                    minItems: 1,
                },
            },
        },
    };

    const [formData, setFormData] = React.useState([]);
    const newdata = () => {
        setFormData([]);
    };
    const download = () => {
        fileDownload(JSON.stringify(formData), "Quests.json");
    };
    const onDrop = useCallback((acceptedFiles) => {
        if (acceptedFiles.length !== 1) {
            alert("请只打开一个文件！");
        } else {
            const reader = new FileReader();
            console.log(
                (reader.onload = () => {
                    // Do whatever you want with the file contents
                    const binaryStr = reader.result;
                    setFormData(JSON.parse(binaryStr));
                })
            );
            reader.readAsText(acceptedFiles[0], "utf-8");
        }
    }, []);

    const { getInputProps, open } = useDropzone({
        onDrop,
        // Disable click and keydown behavior
        noKeyboard: true,
    });
    return (
        <React.Fragment>
            <CssBaseline />
            <AppBar
                position="absolute"
                color="default"
                className={classes.appBar}
            >
                <Toolbar>
                    <Typography variant="h6" color="inherit" noWrap>
                        《少年电脑世界》游戏梦工厂
                    </Typography>
                </Toolbar>
            </AppBar>
            <main>
                <Container>
                    <Typography component="h1" variant="h4" align="center">
                        任务配置生成器
                    </Typography>

                    <React.Fragment>
                        <Form
                            schema={schema}
                            className="col-centered"
                            formData={formData}
                            onChange={(e) => setFormData(e.formData)}
                        >
                            <ButtonGroup
                                color="primary"
                                style={{ paddingTop: "10px" }}
                            >
                                <input {...getInputProps()} />
                                <Button onClick={newdata}>
                                    新建文件(未保存的文件将会被丢失)
                                </Button>
                                <Button onClick={open}>打开文件</Button>
                                <Button onClick={download}>保存文件</Button>
                            </ButtonGroup>
                        </Form>
                    </React.Fragment>
                </Container>
                <Copyright />
            </main>
        </React.Fragment>
    );
}
